'use client'

import { useState, useEffect } from 'react'
import { Anime } from '@/lib/api'
import { AnimeCard } from '@/components/anime-card'
import { useSearchParams } from 'next/navigation'

export default function SearchPage() {
  const searchParams = useSearchParams()
  const query = searchParams.get('q') || ''

  const [animes, setAnimes] = useState<Anime[]>([])
  const [loading, setLoading] = useState(false)

  useEffect(() => {
    if (!query) return

    const fetchSearch = async () => {
      setLoading(true)
      try {
        const res = await fetch(`/api/search?q=${encodeURIComponent(query)}`)
        if (!res.ok) throw new Error('Failed to fetch search results')
        const data = await res.json()

        // Map hasil search supaya sesuai tipe Anime dari lib/api
        const mapped: Anime[] = data.data
          .filter(
            (anime: any, index: number, self: any[]) =>
              self.findIndex(a => a.mal_id === anime.mal_id) === index
          )
          .map((anime: any) => ({
            mal_id: anime.mal_id,
            title: anime.title,
            episodes: anime.episodes,
            images: {
              jpg: {
                image_url: anime.images.jpg.small_image_url,
                small_image_url: anime.images.jpg.small_image_url,
                large_image_url: anime.images.jpg.small_image_url,
              },
            },
          }))

        setAnimes(mapped)
      } catch (err) {
        console.error('Search fetch failed:', err)
        setAnimes([])
      } finally {
        setLoading(false)
      }
    }

    fetchSearch()
  }, [query])

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <h1 className="text-2xl font-bold mb-6">Search results for: <span className="text-primary">{query}</span></h1>

      {loading ? (
        <p>Loading...</p>
      ) : animes.length > 0 ? (
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
          {animes.map((anime) => (
            <AnimeCard key={`${anime.mal_id}`} anime={anime} />
          ))}
        </div>
      ) : (
        <p className="text-muted-foreground">No results found.</p>
      )}
    </div>
  )
}
